.productscreen {
  max-width: 1300px;
  margin: 1rem auto;
  display: flex;
}

.productscreen-left {
  display: flex;
  flex: 0.8;
}

.left-image {
  margin: 1rem;
  flex: 0.6;
}

.left-info {
  margin: 1rem;
  flex: 0.4;
  background-color: white;
  height: fit-content;
  font-size: 0.9rem;
}

.left-name {
  font-weight: bold;
  font-size: 1.3rem;
}

.left-info > p,
.right-info > p {
  padding: 1rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.left-info > p:last-child,
.right-info > p:last-child {
  border: none;
}

.productscreen-right {
  display: flex;
  flex: 0.2;
}

.right-info {
  height: fit-content;
  width: 250px;
  margin: 1rem;
  background-color: white;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
}

.right-info p {
  padding: 1rem;
  font-size: 0.8rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.right-info p > button {
  border: none;
  grid-column: 1/-1;
  padding: 10px 16px;
  background-color: rgb(233,133,0);
  color: white;
  cursor: pointer;
}

.right-info p > a {
  text-decoration: none;
  text-align: center;

  border: none;
  grid-column: 1/-1;
  padding: 10px 16px;
  background-color: rgb(233,133,0);
  color: white;
  cursor: pointer;
}


.right-info select {
  padding: 10px 16px;
}

.right-info p > button:hover {
  background-color: rgb(255, 181, 85);
  color: white;
}

.right-info p > a:hover {
  background-color: rgb(255, 181, 85);
  color: white;
}

.message {
  width: 100%;
  height: 100vh;
  background-color: rgba(0,0,0,0.5);
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.message-content {
  width: max-content;
  height: min-content;
  background-color: white;
  z-index: 150;
  padding: 30px;

  display: flex;
  flex-direction: column;

  justify-content: center;
  /* align-items: center; */
}

.message-content > h2 {
  font-size: 1.0em;
  margin-bottom: 12px;
}

.message-content > button {
  border: none;
  padding: 10px 16px;
  background-color: rgb(0, 206, 45);
  color: white;
  cursor: pointer;
  margin-top: 20px;
}


/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .productscreen {
    flex-direction: column;
  }

  .productscreen-left {
    flex-direction: column;
    flex: 1;
  }

  .left-image {
    flex: 1;
  }

  .left-info {
    flex: 1;
  }

  .productscreen-right {
    flex: 1;
    padding: 1rem;
  }

  .right-info {
    width: 100%;
  }
}
