.loadingscreen {
  display: flex;
  height: 80vh;
  width: 100vw;
  justify-content: center;
  align-items: center;
}

.loading {
  width: 100px;
  height: 100px;
  border: 20px solid rgb(233,133,0);
  border-left: 20px solid rgb(231, 231, 231);
  border-right: 20px solid rgb(231, 231, 231);
  border-radius: 50%;
  animation: spin 2s linear infinite alternate;
}

@keyframes spin {
  0% {transform: rotate(0deg);}
  50% {transform: rotate(360deg);}
}

.cartscreen {
  max-width: 1300px;
  margin: 2rem auto;
  display: flex;
  flex-direction: column;
}

.cartscreen-header {
  margin-bottom: 1rem;
}

.cartscreen-header2 {
  margin-bottom: 1rem;
  padding-top: 20px;
}

.cartscreen-content {
  display: flex;
}

.cartscreen-left {
  flex: 0.7;
  margin-right: 1rem;
  background-color: transparent;
}

.cartscreen-right {
  flex: 0.3;
  background-color: white;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  height: fit-content;
}

.cartscreen-right div {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.cartscreen-right div:last-child {
  border: none;
}

.cartscreen-info p {
  padding: 1rem;
  width: 100%;
}


.cartscreen-right div button {
  border: none;
  width: 100%;
  grid-column: 1/-1;
  padding: 10px 16px;
  background-color: rgb(233,133,0);
  color: white;
  cursor: pointer;
}

.cartscreen-right div button:hover {
  background-color: rgb(255, 181, 85);
  color: white;
}



/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .cartscreen-content {
    max-width: 1300px;
    flex-direction: column;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .cartscreen-content {
    flex-direction: column;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .cartscreen {
    max-width: 800px;
  }

  .cartscreen-content {
    flex-direction: row;
  }
} 

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .cartscreen {
    max-width: 950px;
  }

  .cartscreen-content {
    flex-direction: row;
  }
} 

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .cartscreen {
    max-width: 1200px;
  }

  .cartscreen-content {
    flex-direction: row;
  }
}