.infobox {
  width: 100%;
  height: 100vh;
  background-color: rgba(0,0,0,0.5);
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.infobox-content {
  width: 60%;
  height: min-content;
  background-color: white;
  z-index: 150;
  padding: 30px;

  display: flex;
  flex-direction: column;

  justify-content: center;
  /* align-items: center; */
}

.infobox-content > h2 {
  font-size: 1.0em;
  margin-bottom: 12px;
}

.infobox-content > .productList {
  display: flex;
  width: 100%;

  overflow-x: scroll; /* Hide horizontal scrollbar */
  overflow-y: hidden; /* Add vertical scrollbar */
}